import React from 'react'
import UserIconContainer from '../icons/containers/user'


const Users = (props) => {
    const {levels = [{type:1, nb:0}]} = props;
    const noNumbers = levels.length === 1;
    return (
       <>
           <div className="levels">{
                levels.map((level, idx) => {
                return <div className={`level t${level.type}`}>{!noNumbers && level.nb}</div>
                })
            } </div>
                   

            <style jsx>
                {`  
                    
                    .levels {
                        align-items: center;
                        display: flex;
                        flex-direction: row;
                    }

                    .level {
                        width: 13px;
                        height: 13px;

                        
                        align-items: center;
                        display: flex;
                        justify-content: center;

                        font: 8px/11px Arial;
                        letter-spacing: 0;
                        color: #FFFFFF;

                        border-radius: 3px;

                        margin-right: 2px;
                    }

                    .t1 {
                        background-color: #28B78D;
                        border: 1px solid #14B88D;
                    }

                    .t2 {
                        background-color: #F2B134;
                        border: 1px solid #F4B234;
                    }

                    .t3 {
                        background-color: #ED553B;
                        border: 1px solid #F0553B;
                    }

                    .t4 {
                        background-color: #243743;
                        border: 1px solid #233743;
                    }


                  
                `}

            </style>


        </>

    )
}

export default Users;